import React , {Component} from 'react'
import BaseTable from '@/components/BaseTable'
import BaseContent from '@/components/BaseContent'
import {
    Button,
    Modal,
    Form,
    Input,
    Select
} from 'antd'
import { connect } from 'dva'

const { Option } = Select

const mapStateToProps = ({grade}) => {
    return {...grade}
}
@connect(mapStateToProps)

@Form.create()

class Grade extends Component{

    state = {
        visible: false,
        clickType: 'add',
        editMsg: {}
    }

    componentDidMount() {
        let {
            dispatch
        } = this.props;
        dispatch({
            type: 'grade/getAllMsg'
        })
        
    }

    delMsg = ({grade_id}) => {
        let {
            dispatch
        } = this.props;
        dispatch({
            type: 'grade/delGrade',
            payload: {
                grade_id
            }
        })
    }

    // 
    getColumns = () => {
        const columns = [{
            title: '',
            dataIndex: 'grade_name',
            key: 'grade_name',
        }, {
            title: '',
            dataIndex: 'subject_text',
            key: 'subject_id',
        }, {
            title: '',
            dataIndex: 'room_text',
            key: 'room_id'
        }, {
            title: '',
            render: (record) => {
                return <div>
                    <a onClick={() => this.editMsg(record)}></a>
                    |
                    <a onClick={() => this.delMsg(record)}></a>
                </div>
            }
        }];
        return columns 
    }

    // 
    editMsg = (msg) => {
        this.setState({
            editMsg: {...msg},
            visible: true,
            clickType: 'edit'
        })
    }

    //
    addGrade = () => {
        let {
            dispatch
        } = this.props
        this.setState({
            visible: true,
            clickType: 'add'
        })
        dispatch({
            type: 'grade/getAllSubject'
        })
    }

    //
    handleOk = () => {
        let { validateFields, resetFields } = this.props.form;
        let { clickType, editMsg } = this.state;
        let { dispatch } = this.props;
        validateFields((err, values) => {
            if (err) {
                return
            }
            if (clickType === 'add') {
            
                dispatch({
                    type: 'grade/addGrade',
                    payload: {
                        grade_name: values.grade,
                        room_id: values.room,
                        subject_id:values.course
                    }
                })
            } else {
                dispatch({
                    type: 'grade/editGrade',
                    payload: {
                        grade_id: editMsg.grade_id,
                        subject_id: values.course,
                        room_id: values.room
                    }
                })
            }
            //  
            this.setState({
                visible: false,
                editMsg: {}
            })
            resetFields()
        })
    }

    //
    handleCancel = () => {
        let {
            resetFields
        } = this.props.form
        this.setState({
            visible: false,
            editMsg: {}
        })
        resetFields()
    }

    render(){
        let {
            allGradeMsg,
            form: {
                getFieldDecorator
            },
            allSubject,
            allRoom
        } = this.props;
        let {
            visible,
            editMsg,
            clickType
        } = this.state;
        return <BaseContent>
            
            <BaseTable
                rowKey={record => record.grade_id}
                dataSource={allGradeMsg}
                columns={this.getColumns()}
                pagination={false}
                buttons={[
                    <Button
                        type="primary"
                        icon="plus"
                        size="large"
                        style={{float: 'right'}}
                        onClick={this.addGrade}
                    ></Button>
                ]}
                wrapStyle={{
                    padding: 0,
                }}
            />
            <Modal
                visible={visible}
                title=""
                onOk={this.handleOk}
                onCancel={this.handleCancel}
                footer={[
                    <Button key="back" onClick={this.handleCancel}></Button>,
                    <Button key="submit" type="primary" onClick={this.handleOk}>
                        
                    </Button>,
                ]}
            >
                <Form labelcol={{ span: 5 }} wrappercol={{ span: 12 }}>
                    <Form.Item
                        label=""
                    >
                        {getFieldDecorator('grade', {
                            initialValue: editMsg.grade_name,
                            rules: [{ required: true, message: '' }],
                        })(
                            <Input type="text" placeholder="" disabled={clickType === 'edit'} />
                        )}
                    </Form.Item>
                    <Form.Item
                        label=""
                    >
                        {getFieldDecorator('room', {
                            initialValue: editMsg.room_id,
                            rules: [{ required: true, message: '' }],
                        })(
                            <Select placeholder="">
                                {
                                    allRoom.length > 0 && allRoom.map((val, ind) => {
                                        return <Option value={val.room_id} key={val.room_id}>{val.room_text}</Option>
                                    })
                                }
                            </Select>
                        )}
                    </Form.Item>
                    <Form.Item
                        label=""
                    >
                        {getFieldDecorator('course', {
                            initialValue: editMsg.subject_id,
                            rules: [{ required: true, message: '' }],
                        })(
                            <Select placeholder="">
                                {
                                    allSubject.length > 0 && allSubject.map((val, ind) => {
                                        return <Option key={val.subject_id} value={val.subject_id}>{val.subject_text}</Option>
                                    })
                                }
                            </Select>
                        )}
                    </Form.Item>
                </Form>
            </Modal>
        </BaseContent>
    }
}

export default Grade